[<RequireQualifiedAccess>]
module Samples.Localization

open Fable.Core.Experimental
open Feliz
open Feliz.MaterialUI
open Feliz.MaterialUI.MaterialTable

type private RowData =
    { name: string
      surname: string
      birthYear: int
      birthCity: int }

let render = React.functionComponent (fun () ->
    let theme = Styles.useTheme()

    Mui.materialTable [
        prop.style [ style.backgroundColor theme.palette.background.``default`` ]
        materialTable.title "Pdtine Tabloye Kurd"
        materialTable.columns [
            columns.column [
                column.title "Nav"
                column.field<RowData> (fun rd -> nameof rd.name)
            ]
            columns.column [
                column.title "Panav"
                column.field<RowData> (fun rd -> nameof rd.surname)
            ]
            columns.column [
                column.title "Sal Zayn"
                column.field<RowData> (fun rd -> nameof rd.birthYear)
                column.type'.numeric
            ]
            columns.column [
                column.title "Che Zayn"
                column.field<RowData> (fun rd -> nameof rd.birthCity)
                column.lookup<int,string> [ 
                    (34, "stanbul")
                    (63, "anlurfa") 
                ]
            ]
        ]
        materialTable.data [
            { name = "Mehmet"
              surname = "Baran"
              birthYear = 1987
              birthCity = 63 }
            { name = "Zerya Betl"
              surname = "Baran"
              birthYear = 2017
              birthCity = 34 }
        ]
        materialTable.localization [
            localization.body [
                localization.body.emptyDataSourceMessage "Guherandinn ku titek tune"
            ]
            localization.toolbar [
                localization.toolbar.searchTooltip "Lgern"
            ]
            localization.pagination [
                localization.pagination.labelRowsSelect "Xet"
                localization.pagination.labelDisplayedRows " {from}-{to} xete {count}"
                localization.pagination.firstTooltip "Rpele Bern"
                localization.pagination.previousTooltip "Rpele Ber"
                localization.pagination.nextTooltip "Rpele Pit"
                localization.pagination.lastTooltip "Rpele Tal"
            ]
        ]
    ])
